@import url(https://fonts.googleapis.com/css?family=Fira+Sans|Oxygen:300,400|Oxygen+Mono&display=swap);
:root {
  --headfont: 'Fira Sans', sans-serif;
  --bodyfont: 'Oxygen', sans-serif;
  --monospace: 'Oxygen Mono', monospace;
  --white: #ffffff;
  --offwhite: #f9f9fc;
  --darkgray: #1b1d1e;
  --gray: #4D4D4D;
  --lightgray: #999999;
  --green: #C2E15F;
  --orange: #FDA333;
  --purple: #D3A4F9;
  --red: #fb5344;
  --pinkred: #FB4485;
  --blue: #6CE0F1;
  --salmon: #fa8a90;
  --salmon-light: #fa9ba1;
  --cyan : #8afaf4;
  --shadow: #cccccc;

  color: #4D4D4D;

  color: var(--gray);
  background-color: #f9f9fc;
  background-color: var(--offwhite);

  font-family: 'Oxygen', sans-serif;

  font-family: var(--bodyfont);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  padding: 0;
  margin: 0;
}

html, body {
  width: 100%;
  min-height: 100%;
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6, th {
  font-family: 'Fira Sans', sans-serif;
  font-family: var(--headfont);
  margin-bottom: 0.5rem;
}

h1 { font-size: 3.0rem; }
h2 { font-size: 2.5rem; }
h3 { font-size: 2.0rem; }
h4, th { font-size: 1.5rem; }
h5 { font-size: 1.0rem; }
h6 { font-size: 0.5rem; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
  padding: 3rem 1.5rem;
}

ul, ol {
  margin-left: 1rem;
}


.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  padding: 2rem 1rem;
  background-color: var(--white);
  box-shadow: 0px 0px 10px var(--shadow);
  border-radius: 10px;
  margin: 0.5rem 1rem;
  min-width: 250px;
}

.card h4 {
  text-align: center;
}

.VisualPage {
  width: 95vw;
  max-width: 1250px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto auto auto;
  grid-template-areas: "header header" "about complexity" "controls controls" "visual visual";
}

@media only screen and (max-width: 750px) {
  .VisualPage {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto auto auto;
    grid-template-areas: "header" "about" "controls" "visual" "complexity";
  }
}

.VisualPage > h1 {
  grid-area: header;
  text-align: center;
  border-bottom: 1px solid var(--darkgray);
}

.VisualPage .About {
  grid-area: about;
}

.VisualPage .Complexity {
  grid-area: complexity;
}

.VisualPage .Complexity table {
  margin: 0 auto;
}

.VisualPage .Complexity th {
  padding-bottom: 0.25em;
  border-bottom: 1px solid var(--darkgray);
}

/* Prevent wrapping of second column i.e. the complexity function */
.VisualPage .Complexity td:nth-of-type(2) {
  white-space: nowrap;
}

.VisualPage .Controls {
  grid-area: controls;
  display: flex;
  flex-direction: column;
}

.VisualPage .Controls .ControlGroup {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.VisualPage .Controls .ControlGroup * {
  margin: auto 0.5rem;
}

.VisualPage .Visual {
  grid-area: visual;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: auto;
}

.linked-list {
  width: 100%;
  display: flex;
}

.linked-list-node {
  background-color: var(--white);
  box-shadow: 0px 0px 10px var(--shadow);
  padding: 1.5rem 0.75rem;
  margin: auto 1rem;
  border-radius: 5px;
  transition: transform ease-in-out 0.25s;
  position: relative;
}

.linked-list-node::after {
  display: block;
  content: '';
  background-color: var(--gray);
  width: 2rem;
  position: absolute;
  height: 1rem;
  left: 100%;
  top: calc(50% - 0.5rem);
  -webkit-clip-path: polygon(0% 37.5%, 40% 37.5%, 40% 0%, 65% 37.5%, 100% 37.5%, 100% 62.5%, 65% 62.5%, 40% 100%, 40% 62.5%, 0% 62.5%);
          clip-path: polygon(0% 37.5%, 40% 37.5%, 40% 0%, 65% 37.5%, 100% 37.5%, 100% 62.5%, 65% 62.5%, 40% 100%, 40% 62.5%, 0% 62.5%);
}

.linked-list-node:last-child::after {
  display: none;
}

.linked-list-node.highlight {
  transform: scale(1.25);
}

.linked-list-node.highlight::before, .linked-list-node.highlight::before {
  transform: scale(0.8) translateX(-4px);
}

.stack {
  display: flex;
  width: 50%;
  min-height: 20rem;
  padding-top: 5rem;
  border: 5px solid var(--gray);
  border-top: none;
  flex-direction: column-reverse;
  flex-wrap: nowrap;
  margin: 0 auto;
}

.stack-node {
  overflow: hidden;
  background-color: var(--white);
  box-shadow: 0px 0px 10px var(--shadow);
  margin: 0.5rem 1rem;
  padding: 1rem 0.5rem;
  border-radius: 4px;
  transition: all 0.25s ease-out;
  text-align: center;
}

.stack-node-appear {
  opacity: 0;
  transform: translateY(-50px);
}

.stack-node-appear-active {
  opacity: 1;
  transform: translateY(0);
}

.stack-node-exit {
  opacity: 1;
  transform: translateY(0);
}

.stack-node-exit-active {
  opacity: 0;
  transform: translateY(-50px);
}

.queue {
  display: flex;
  width: 100%;
  height: 75px;
  border-top: 5px solid var(--gray);
  border-bottom: 5px solid var(--gray);
  flex-direction: row-reverse;
  flex-wrap: nowrap;
}

.queue-node {
  background-color: var(--white);
  box-shadow: 0px 0px 10px var(--shadow);
  margin: 0.5rem 1rem;
  padding: 1rem 0.5rem;
  border-radius: 4px;
  transition: all 0.25s ease-out;
}

.queue-node-appear {
  opacity: 0;
  transform: translateX(-500%);
}

.queue-node-appear-active {
  opacity: 1;
  transform: translateX(0);
}

.queue-node-exit {
  opacity: 1;
  transform: translateY(0);
}

.queue-node-exit-active {
  opacity: 0;
  transform: translateY(50px);
}

.array {
  display: flex;
  flex-direction: row;
  wrap: nowrap;
  height: 1.5em;
  font-size: 4rem;
  font-family: var(--monospace);
  align-items: center;
}

.array::before {
  display: block;
  content: '[';
  font-size: 1.5em;
}

.array::after {
  display: block;
  content: ']';
  font-size: 1.5em;
}

.array-index {
  min-width: 1ch;
  height: 1em;
  margin: 0 0.5ch;
  position: relative;
  line-height: 1em;
}

.array-index::after {
  display: block;
  content: ',';
  position: absolute;
  right: -0.75ch;
  bottom: -0.4em;
  font-size: 0.75em;
}

.array-index:last-child {
  margin-right: 0;
}

.array-index:last-child::after {
  display: none;
}

.node {
    cursor: pointer;
  }
  
  .node circle {
    fill: #fff;
    stroke: steelblue;
    stroke-width: 1.5px;
  }
  
  .node text {
    font: 10px sans-serif;
  }
  
  .link {
    fill: none;
    stroke: #ccc;
    stroke-width: 1.5px;
  }
.area {
	padding:30px;
	border:1px solid blue;
	margin-top:20px;
	margin-bottom:20px;
}

input[type="text"], input[type="number"] {
	width: 300px;
	height: 40px;
	font-size: 30px;
	margin: 0px;
	padding: 10px;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input[type="number"] {
    -moz-appearance: textfield;
}

button {
	width: 100px;
	height: 40px;
	border: 0px;
	background-color: green;
	color: white;
	margin: 0px;
	padding: 0px;
}


div.custom {
	width: 50px;
	text-align: center;
	margin: 15px;
	color: #fff;
	position: relative;
	background-color: #000;
	padding: 20px;
	display: inline-block;
	font-size: 18px;
	border-radius: 50px;
}


@-webkit-keyframes black-fade { 
	0% {
		background: #ccc;
	}
	100% {
		background:#000;
	}
}


@keyframes black-fade { 
	0% {
		background: #ccc;
	}
	100% {
		background:#000;
	}
}

.highlight {
	-webkit-animation: black-fade 1s ease-in 1;
	animation: black-fade 1s ease-in 1;
}

.border-center {
	width: 100%;
	border: none 1px solid;
	position: relative;
}

.border-center:before {
	content: ' ';
	position: absolute;
	bottom: 50%;
	border-bottom: 5px brown solid;
	width: 100%;
	z-index: 0;
	
	
}

@-webkit-keyframes focus-fade { 
	0% {
		background: yellow;
	}
	100% {
		background:#000;
	}
}

@keyframes focus-fade { 
	0% {
		background: yellow;
	}
	100% {
		background:#000;
	}
}

.focus {
	background: blue;
    -webkit-animation: focus-fade 1s ease-in 1;
	animation: focus-fade 1s ease-in 1;
	border-bottom:15px solid red;
}

.selected {
	border-bottom:15px solid red;
	background-color: #83fcee;
}

#bigotable tr td {
	padding:10px;
}

#maintable {
border:1px solid #000;
}
#maintable tr:nth-child(odd) td{
	background-color: #fff;
	height:40px;
	vertical-align: top;
}
#maintable tr:nth-child(even) td{
	background-color: #eff1f4;
	height:40px;
	vertical-align: top;
}



