.area {
	padding:30px;
	border:1px solid blue;
	margin-top:20px;
	margin-bottom:20px;
}

input[type="text"], input[type="number"] {
	width: 300px;
	height: 40px;
	font-size: 30px;
	margin: 0px;
	padding: 10px;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input[type="number"] {
    -moz-appearance: textfield;
}

button {
	width: 100px;
	height: 40px;
	border: 0px;
	background-color: green;
	color: white;
	margin: 0px;
	padding: 0px;
}


div.custom {
	width: 50px;
	text-align: center;
	margin: 15px;
	color: #fff;
	position: relative;
	background-color: #000;
	padding: 20px;
	display: inline-block;
	font-size: 18px;
	-moz-border-radius: 50px;
	-webkit-border-radius: 50px;
	border-radius: 50px;
}


@-webkit-keyframes black-fade { 
	0% {
		background: #ccc;
	}
	100% {
		background:#000;
	}
}


@keyframes black-fade { 
	0% {
		background: #ccc;
	}
	100% {
		background:#000;
	}
}

.highlight {
	-webkit-animation: black-fade 1s ease-in 1;
	animation: black-fade 1s ease-in 1;
}

.border-center {
	width: 100%;
	border: none 1px solid;
	position: relative;
}

.border-center:before {
	content: ' ';
	position: absolute;
	bottom: 50%;
	border-bottom: 5px brown solid;
	width: 100%;
	z-index: 0;
	
	
}

@-webkit-keyframes focus-fade { 
	0% {
		background: yellow;
	}
	100% {
		background:#000;
	}
}

@keyframes focus-fade { 
	0% {
		background: yellow;
	}
	100% {
		background:#000;
	}
}

.focus {
	background: blue;
    -webkit-animation: focus-fade 1s ease-in 1;
	animation: focus-fade 1s ease-in 1;
	border-bottom:15px solid red;
}

.selected {
	border-bottom:15px solid red;
	background-color: #83fcee;
}

#bigotable tr td {
	padding:10px;
}

#maintable {
border:1px solid #000;
}
#maintable tr:nth-child(odd) td{
	background-color: #fff;
	height:40px;
	vertical-align: top;
}
#maintable tr:nth-child(even) td{
	background-color: #eff1f4;
	height:40px;
	vertical-align: top;
}



/*
	Hash Table
*/

.hashtable {
	margin :5px;
	padding: 5px;
}

.hashtable td {
	border:1px solid #000;

	width: 50px;
	height: 50px;
	padding:10px;
	margin:40px;
	text-align: center;
	vertical-align: middle;
}

.staging {
	width:50%;
	height: 200px;
	text-align: center;

}

.item {
	width:150px;
	height:150px;
	text-align: center;
	border: 1px solid #ccc;
}

.item1 {
	text-align: center;
	color: green;
}
